<template>
  <el-form :model="signinForm" ref="signinForm" :rules="rules">
    <el-form-item label="Email" prop="email">
      <el-input v-model="signinForm.email" type="email"></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
      <el-input v-model="signinForm.password" type="password"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="validateForm('signinForm')"></el-button>
    </el-form-item>
  </el-form>
  </form>
</template>

<script>
export default {
  data() {
    return {
      signinForm: {
        email: '',
        password: ''
      },
      rules: {
        email: [
          { required: true, message: '   ', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '   ', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    validateForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.signinUser();
        } else {
          return false;
        }
      });
    },
    signinUser: function() {
      firebase.auth().signInWithEmailAndPassword(this.signinForm.email, this.signinForm.password)
        .then(response => {
          console.log(response);
        })
    }
  }
}
</script>

<style lang="scss"></style>





